@keyframes rotate {
  0% {
    transform: translateY(-50%)rotate(0);
  }

  100% {
    transform: translateY(-50%)rotate(360deg);
  }
}

.page__body {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

.page__stop-watch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
}

.stopwatch {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

.stopwatch__minutes {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
  transform: translateY(-50%);
}

.stopwatch__seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
  transform: translateY(-50%);
}

.stopwatch--speed-up .stopwatch__minutes {
  animation: 600s steps(60, end) infinite rotate;
}

.stopwatch--speed-up .stopwatch__seconds {
  animation: 10s linear infinite rotate;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}
